@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/earlyaccess/nats.css);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400&display=swap);
@import url(https://fonts.googleapis.com/earlyaccess/nats.css);
body {
  margin: 0;
  font-family: Noto Sans KR, Righteous, -apple-system, NATS, BlinkMacSystemFont,
    Segoe UI, Roboto, sans-serif, Helvetica Neue, Arial, Apple Color Emoji,
    Segoe UI Emoji;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  width: 100%;
  height: 100%;
  position: fixed;
  overflow: hidden;
  touch-action: none;
  margin: 0;
  padding: 0;
  @media (min-width: 768px) {
    font-size: 16px;
  }
}

code {
  font-family: Righteous, NATS, source-code-pro, Menlo, Monaco, Consolas,
    "Courier New", monospace;
}

.IntroItem {
  position: relative;
  width: 100%;
  height: 20vh;
  margin-top: 1vh;
  margin-bottom: 1vh;
  padding-bottom: 1vh;
  padding-top: 1vh;
}

.IntroItem_day {
  position: absolute;
  top: 2vh;
  width: auto;
  height: 1vh;
  right: 0;
  margin-right: 3vw;
  font-family: NATS;
  font-style: normal;
  font-weight: 600;
  font-size: 5vh;
  line-height: 4.5vh;
  color: #033540;
}

.IntroItem_content {
  position: absolute;
  display: inline-block;
  height: auto;
  width: auto;
  white-space: pre-wrap;
  background: #4ba4f2;
  box-shadow: 0px 0.5vh 0.5vh rgba(0, 0, 0, 0.2);
  border-radius: 1.8vw;
  top: 7vh;
  right: 0;
  font-family: NATS;
  font-style: normal;
  font-weight: 500;
  font-size: 2.7vh;
  line-height: 4vh;
  right: 0;
  margin-right: 2vw;
  margin-left: 2vw;
  padding: 3vh 2vw;
  /* or 125% */
  text-align: right;
  color: #f2f2f2;
}

.ProjectItem {
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  position: relative;
  width: 27vw;
  height: 33vh;
  background: #0468bf;
  border-radius: 2vw;
  margin: 1vw;
  margin-bottom: 4vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 1px 3px 2px rgba(0, 0, 0, 0.25);
}

.ProjectItem_img {
  position: absolute;
  background: #e4e4e4;
  left: 0;
  box-shadow: 1px 3px 2px rgba(0, 0, 0, 0.25);
  top: 0;
  width: 100%;
  object-fit: cover;
  height: 70%;
}

.ProjectItem_day {
  position: absolute;
  bottom: 5.5vh;
  font-weight: bord;
  font-family: Noto Sans KR, sans-serif;
  font-size: 3vh;
  line-height: 3vh;
  color: white;
}

.ProjectItem_content {
  position: absolute;
  bottom: 2.5vh;
  font-family: Noto Sans KR, sans-serif;
  color: white;
  font-size: 2vh;
  line-height: 2vh;
}

.SkillItem {
  position: relative;
  width: 95%;
  height: 90%;
}

.SkillItem_collect {
  position: absolute;
  right: 0;
  width: 70%;
  height: 100%;
}
.SkillItem_name {
  position: absolute;
  top: 5px;
  position: absolute;
  width: 87%;
  height: 30px;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 3.5vh;
  color: #000000;
}

.SkillItem_pro {
  position: absolute;
  width: auto;
  height: auto;
  right: 0;
  top: 3vh;
  text-align: right;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 2.5vh;
  line-height: 2.5vh;
  color: #000000;
}

.SkillButton_div {
  position: absolute;
  width: auto;
  height: auto;
  bottom: 3.5vh;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.SkillButton {
  position: relative;
  background: azure;
}

.ProjectTopDiv {
  position: relative;
  width: 100%;
  height: 28vh;
  margin-top: 10vh;
  margin-bottom: 20px;
}

.ProjectHeaderText {
  position: relative;
  left: 5%;
  height: 12vh;
  font-family: NATS;
  font-style: normal;
  font-weight: bolder;
  font-size: 7vh;
  color: #000000;
}

.ProjectLinkText {
  position: relative;
  font-family: NATS;
  font-style: normal;
  font-weight: normal;
  font-size: 4vh;
  text-align: right;
  color: #5c5c5c;
  z-index: 2;
  margin-bottom: 13vh;
  text-decoration: underline;
  text-underline-position: under;
}

.Homeimg{
    width:100vw;
    height:100vh;
    background: url(/myhome/static/media/home.d26da7a5.jpg) no-repeat center center fixed;
    background-size: cover;

}

.home_line{
    position:absolute;
    background-color: white;
    height: 0.1vh;
    width: 47vw;
    left:45vw;
    top:50vh;
}
.IntroBg {
  position: absolute;
  width: 100vw;
  height: 10vh;
  margin-top: 9vh;
  left: 0;
}

.intro_Picture {
  position: absolute;
  width: 10vw;
  height: 21vh;
  right: 10%;
  top: 15%;
  border-radius: 50%;
  background-image: url(/myhome/static/media/intro.cd7d8007.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  border: 5px solid #ffffff;
  box-sizing: border-box;
  object-fit: cover;
}

.IntroButton {
  position: absolute;
  top: 8%;
  width: 12%;
  height: 70px;
  border: none;
  color: #223440;
  padding-bottom: 25px;
  font-family: "Noto Sans KR";
  background: #f2f2f2;
  border-radius: 20px;
}


.bottom {
  height:2vh;
  width: 100vw;
  position:absolute;
  bottom: 0;
  margin: 0;
  padding: 1vh;
  background:red;
  float:right;
  font-size: 10px;
  color:#282c34;
}


.navbar {
  display: flex;
  width: 100%;
  height: 9vh;
  position: fixed;
  background: #ffffff;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.25);
  padding: 5px 2vw;
  color: black;
  align-items: center;
  z-index: 1;
}

.nav_Text {
  position: absolute;
  font-family: Righteous;
  font-style: normal;
  font-weight: normal;
  font-size: 4.5vh;
  color: #000000;
}

.nav_menu {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  right: 5vw;
  height: auto;
  width: auto;
  top: 0;
  font-family: NATS;
  font-size: 2vw;
  color: #000000;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 0;
  display: inline-block;
}

.nav_menu li {
  padding: 8px 20px;
  float: right;
}

.nav_menu li:hover {
  color: #035aa6;
}

a {
  text-decoration: none;
  color: black;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 768px) {
  .navbar {
    display: flex;
    width: 100%;
    top: 0;
    left: 0;
    position: relative;
    background: chocolate;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding: 5px 20px;
    color: black;
    align-items: center;
  }
  .nav_text {
    position: absolute;
    font-family: Righteous;
    flex-direction: column;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    color: #000000;
  }
  .nav_menu {
    left: 0;
    position: absolute;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    color: #000000;
    flex-direction: column;
    align-items: center;
    left: 0;
    width: 100vw;
  }
  .nav_menu ul {
    display: flex;
    flex-direction: column;
    width: 100vw;
    text-align: center;
    padding: 2px 10px;
  }
  .bottom {
    background-color: blue;
    height: 30px;
    width: 100%;
    position: absolute;
    bottom: 0;
    font-size: 10px;
    color: #282c34;
  }
}

